import React from 'react';

class Search extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            searchUrl:'//www.google.com/search'
        }
    }

    render(){
        return (
            <div className="search">
                <div className="content">
                    <form className="search-form" action={this.state.searchUrl} target="_blank">
                        <input type="text" name="q" maxLength={20} placeholder=""/>
                        <input type="hidden" name="sitesearch" value="http://lewis.suclub.cn"/>
                    </form>
                </div>
            </div>
        )
    }
}

export default Search


