import React from 'react';
import {Link} from 'react-router-dom';
import '../../style.css'

function Logo() {
    return (
        <a className="logo" href='/'>
            <span></span>
        </a>
    )
}

class RightHeader extends React.Component{
    constructor(props){
        super(props);
        this.changeStatus = this.changeStatus.bind(this);
        this.state = {
            status:[true,false,false,false,false],
            items : ["","","","",""],
            path:["/","/tag","/date","/about","/login"],
            current:0
        }
    }

    changeStatus(i){
        let current = this.state.current;
        let status = this.state.status;
        status[current] = false;
        status[i] = true;
        this.setState({
            current:i,
            status:status
        });
    }

    render(){
        return(
            <ul className="right-header">
                {this.state.items.map((name,i)=><HeaderItem key={i} name={name} path={this.state.path[i]} status={this.state.status[i]} onClick={()=>{this.changeStatus(i)}}/>)}
            </ul>
        )
    }
}

function HeaderItem (props){
    return(
        <li className="nav-item">
            <Link to={props.path} className={props.status?'active':'unActive'} onClick={props.onClick}>
                {props.name}
            </Link>
        </li>
    )
}



function Header() {
    return(
        <header>
            <div className="header-container">
                <Logo/>
                <RightHeader/>
            </div>
        </header>
    )
}

export default Header;


