import React from 'react';
import {Switch,Route} from 'react-router-dom';
import Tag from '../main/tag';
import ByDate from '../main/byDate';
import About from '../main/about';
import BaseHome from "../main/baseHome";
import TagOne from "../main/tagOne";
import ArticleDetail from "../main/articleDetail";
import Classification from "../main/classification";
import Login from '../main/admin/login';
import Admin from '../main/admin/admin';

const Main = () => (
    <Switch>
        <Route exact path='/' component={BaseHome}/>
        <Route path='/tag/:tagName' component={TagOne}/>
        <Route path='/article/:articleId' component={ArticleDetail}/>
        <Route path='/classification/:classificationName' component={Classification}/>
        <Route path='/tag' component={Tag}/>
        <Route path='/date' component={ByDate}/>
        <Route path='/about' component={About}/>
        <Route path='/login' component={Login}/>
        <Route Path='/admin' component={Admin}/>
    </Switch>
);

export default Main;


