import React from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import config from '../../config';
import Search from './serach';
import CategoryList from './category';

import ArticleMeta from '../public/articleMeta';
import Paging from '../public/paging';

function ArticleName(props) {
    return (
        <Link to={'/article/' + props.articleId}>
            <h3 className="post-title">
                {props.articleName}
            </h3>
        </Link>
    )
}

function ArticleImg(props) {
    return (
        <div className='post-img'><img src={props.articleImgSrc} alt={props.alt}/></div>
    )
}

function ArticleContent(props) {
    return (
        <div className='post-content'>
            <p>{props.articleContent}</p>
        </div>
    )
}


function Article(props) {
    return (
        <section>
            <ArticleName articleName={props.articleName} articleId={props.articleId}/>
            <ArticleImg articleImgSrc={props.articleImgSrc}/>
            <ArticleContent articleContent={props.articleContent}/>
            <ArticleMeta
                date={props.articleDate}
                classification={props.classification}
                tag={props.articleTags}
            />
        </section>
    )
}

class BaseHome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            articles: [
                {
                    articleId: 1,
                    articleName: '123',
                    articleImgSrc: '',
                    articleContent: 'sdfhjaljsdhf',
                    articleDate: '2018.4.9',
                    classification: ['123'],
                    articleTags: ['1', '2', '3']
                }
            ],
            categories: [],
            page: 1,
            allowClick:true
        };
        this.getPreviousPage = this.getPreviousPage.bind(this);
        this.getNextPage = this.getNextPage.bind(this);
    }

    componentDidMount() {
        axios.get(`${config.host}/article/get?page=${this.state.page}`)
            .then((res) => {
                this.setState({
                    articles: res.data.data
                })
            })
            .catch((err) => {
                console.log(err);
            });
        axios.get(`${config.host}/category/get`)
            .then((res) => {
                this.setState({
                    categories: res.data.data
                })
            }).catch((err) => {
            console.log(err);
            })
    }

    getNextPage() {
        if(this.state.allowClick){
            this.setState({allowClick:false});
            this.setState({page: ++this.state.page});
            axios.get(`${config.host}/article/get?page=${this.state.page}`)
                .then((res) => {
                    if (res.data.code === 0) {
                        this.setState({
                            articles: res.data.data
                        });
                        this.setState({allowClick:true});
                    }
                    else {
                        this.setState({page:--this.state.page,allowClick:true});
                    }
                })
                .catch((err) => {
                    this.setState({page:--this.state.page,allowClick:true});
                    console.log(err);
                })
        }
    }

    getPreviousPage() {
        if (this.state.page > 1 && this.state.allowClick) {
            this.setState({allowClick:false});
            this.setState({page: --this.state.page});
            axios.get(`${config.host}/article/get?page=${this.state.page}`)
                .then((res) => {
                    if (res.data.code === 0) {
                        this.setState({
                            articles: res.data.data
                        });
                        this.setState({allowClick:true});
                    }else {
                        this.setState({page:++this.state.page,allowClick:true});
                    }
                })
                .catch((err) => {
                    this.setState({page:++this.state.page,allowClick:true});
                    console.log(err);
                })
        }
    }

    render() {
        return (
            <div>
                <main id='index'>
                    <div className='posts-list'>
                        {this.state.articles.map(
                            (article, i) =>
                                <Article
                                    key={i}
                                    articleId={article.articleId}
                                    articleName={article.articleName}
                                    articleImgSrc={article.articleImgSrc}
                                    articleContent={article.articleContent}
                                    articleDate={article.articleDate}
                                    classification={article.classification}
                                    articleTags={article.articleTags}
                                />
                        )}
                    </div>
                    <aside>
                        <Search/>
                        <CategoryList categories={this.state.categories}/>
                    </aside>
                </main>
                <Paging getPrePage={this.getPreviousPage} getNextPage={this.getNextPage}/>
            </div>
        )
    }

}

export default BaseHome;


