import React from 'react';
import axios from "axios";
import config from "../../../config";
import { Redirect } from 'react-router-dom';
import md5 from 'md5';

class Login extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            username:'',
            password:'',
            status:0
        };
        this.login = this.login.bind(this);
        this.setPassword = this.setPassword.bind(this);
        this.setUsername = this.setUsername.bind(this);
    }

    login(){
        axios.post(`${config.host}/admin/login`,{
            username:this.state.username,
            password:this.state.password
        })
            .then((res)=>{
                if(res.data.code === 0){
                    this.setState({status:1});
                }else {
                    this.setState({status:2});
                }
            })
            .catch((err)=>{
                console.log(err);
            })
    }

    setUsername(e){
        this.setState({username:e.target.value});
    }

    setPassword(e){
        this.setState({password:md5(e.target.value)});
    }

    render(){
        if(this.state.status === 0){
            return(
                <main id='post'>
                    <div className='content'>
                        <article>
                            <div className='divCenter'><input type='text' placeholder='' onChange={this.setUsername} className='login'/></div>
                            <div className='divCenter'><input type='password' placeholder='' onChange={this.setPassword} className='login'/></div>
                            <div className='divCenter'><a onClick={this.login}><button className='login' ></button></a></div>
                        </article>
                    </div>
                </main>
            )
        }else if(this.state.status === 1){
            return(
                <Redirect to='/admin'/>
            )
        }else {
            return(
                <Redirect to='/'/>
            )
        }
    }
}

export default Login;



