import React from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import config from '../../config';

function TagList(props) {
    return (
        <div className='tag-list'>
            <span className='fa fa-tag listing-seperator' id={props.name}>
                <span className='tag-text'>{props.name}</span>
            </span>
            {props.articles.map((name) =>
                <Link to={'/article/' + name.articleId}>
                    <h3 className="post-title">
                        {name.articleName}
                    </h3>
                </Link>)
            }
        </div>
    )
}

class TagOne extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tag: {
                name: '',
                articles: [{articleId: 0, articleName: ''}]
            }
        }
    }

    componentDidMount() {
        axios.post(`${config.host}/tag/getOne`, {tagName: this.props.match.params.tagName})
            .then((res) => {
                this.setState({
                    tag: res.data.data
                });
                console.log(res.data.data);
            })
            .catch((err) => {
                console.log(err);
            })
    }

    render() {
        return (
            <main id='tags'>
                <div className='tags-container'>
                    <TagList name={this.state.tag.name} articles={this.state.tag.articles}/>
                </div>
            </main>
        )
    }
}

export default TagOne;


