import React from 'react';
import '../../style.css';

function CopyRight(props) {
    return(
        <div className="copyright">
            {props.copyRight}
            <a href="https://github.com/lewis-geek/hexo-theme-Aath"> Aath</a>
        </div>
    )
}

class Footer extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            copyRight:' 2018 | Powered by  Theme by '
        }
    }

    render(){
        return(
            <footer>
                <CopyRight copyRight={this.state.copyRight}/>
            </footer>
        )
    }
}

export default Footer;


