import React, { Component } from 'react';
import Milk from 'react-milkdown';

import 'font-awesome/css/font-awesome.min.css'; // milkdown uses font-awesome, you can ignore this if your project already import font-awesome

class Milkdown extends Component {
    state = {
        value: "",
        first:true,
    };

    onChange = value => {
        if(this.state.first){
            value = localStorage.getItem('articleContent') + value;
            this.setState({ value:value,first:false });
            localStorage.setItem('articleContent',value);
        }else {
            this.setState({ value:value });
            localStorage.setItem('articleContent',value);
        }
        return value;
    };

    render() {
        const { value } = this.state;
        return (
            <div>
                <Milk
                    value={value}
                    onChange={this.onChange}
                    style={{margin: "0 auto"}}
                />
                <div className='divCenter'><button className='login' onClick={()=>{this.props.send(this.state.value)}}></button></div>
            </div>
        )
    }
}

export default Milkdown;


