import React from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import config from "../../config";

function ArticleList(props) {
    return(
        <li>
            <time>{props.article.createdTime}</time>
            <Link to={`article/${props.article.articleId}`}>
                <span>{props.article.articleName}</span>
            </Link>
        </li>
    )
}

function YearList(props) {
    return(
        <div className='year-list'>
            <h3>{props.year}</h3>
            <ul>
                {props.articles.map((article)=><ArticleList article={article}/>)}
            </ul>
        </div>
    )
}

class ByDate extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            articlesByYear:[]
        }
    }

    componentDidMount(){
        axios.get(`${config.host}/byDate`)
            .then((res)=>{
                this.setState({
                    articlesByYear:res.data.data
                })
            })
            .catch((err)=>{
                console.log(err);
            })
    }

    render(){
        return(
            <main id='archive'>
                <div className='content'>
                    {this.state.articlesByYear.map((item)=><YearList year={item.year} articles={item.articles}/>)}
                </div>
            </main>
        )
    }
}

export default ByDate;


