import React from 'react';
import {Link} from 'react-router-dom';

function Timer(props) {
    return(
        <time>{props.date}</time>
    )
}

function Classification(props) {
    return(
        <div className='article-category'>
            {props.classification.map((name,i)=><Link to={'/classification/'+name} key={i}>{name}</Link>)}
        </div>
    )
}

function ArticleTag(props) {
    return(
        <Link to={'/tag/' + props.name} className='tag'>
            {props.counter===0?'':''}
            {props.name}
        </Link>
    )
}

function ArticleMeta(props) {
    return(
        <div className='post-meta'>
            <Timer date={props.date}/>
            |
            <Classification classification={props.classification}/>
            |
            {props.tag.map((name,i)=><ArticleTag key={i} name={name} counter={i}/>)}
        </div>
    )
}

export default ArticleMeta;


