import React from 'react';
import axios from 'axios';
import {Link} from 'react-router-dom';
import config from '../../config';

function TagCloud(props) {
    return(
        <div className='tags' id='tag_cloud'>
            {props.tags.map((name)=><a href={`#${name.name}`} title={name.name} rel={name.articles.length}>{name.name}</a>)}
        </div>
    )
}

function TagList(props) {
    return(
        <div className='tag-list'>
            <span className='fa fa-tag listing-seperator' id={props.name}>
                <span className='tag-text'>{props.name}</span>
            </span>
            {props.articles.map((name)=>
                <Link to={'/article/'+name.articleId}>
                    <h3 className="post-title">
                        {name.articleName}
                    </h3>
                </Link>)
            }
        </div>
    )
}

class Tag extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            tags:[]
        }
    }

    componentDidMount(){
        axios.get(`${config.host}/tag/all`)
            .then((res)=>{
                this.setState({
                    tags:res.data.data
                })
            })
            .catch((err)=>{
                console.log(err);
            })
    }

    render(){
        return(
            <main id='tags'>
                <div className='tags-container'>
                    <TagCloud tags={this.state.tags}/>
                    {this.state.tags.map((name)=><TagList name={name.name} articles={name.articles}/>)}
                </div>
            </main>
        )
    }
}

export default Tag;


