import React from 'react';
import marked from "marked";
import axios from "axios";
import config from "../../config";

class About extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            about:''
        }
    }

    componentDidMount(){
        axios.get(`${config.host}/about`)
            .then((res)=>{
                this.setState({
                    about:res.data.data
                })
            })
            .catch((err)=>{
                console.log(err);
            })
    }

    render(){
        return(
            <main id='post'>
                <div className='content'>
                    <article>
                        <section className='markdown-body'>
                            <div dangerouslySetInnerHTML={{__html: marked(this.state.about)}}/>
                        </section>
                    </article>
                </div>
            </main>
        )
    }
}

export default About;


