import React from 'react';
import axios from "axios";
import config from "../../../config";
import Milkdown from '../../markdown/markdown';

class Admin extends React.Component{
    constructor(props){
        super(props);
        this.send = this.send.bind(this);
    }

    send(value){
        let divide = /([^#]+)(#[\s\S]+)/;
        let arrays = divide.exec(value);
        let info = /^:([\s\S]*):([\s\S]*):([\s\S]*):([\s\S]*):([\s\S]*)/;
        console.log(arrays[1].replace(/[\r\n]/g, ""));
        let infoArrays = info.exec(arrays[1].replace(/[\r\n]/g, ""));
        console.log(infoArrays);
        axios.post(`${config.host}/admin/create`,{
            name:infoArrays[1],
            content:infoArrays[2],
            tags:infoArrays[3].split(','),
            category:infoArrays[4],
            imgSrc:infoArrays[5],
            markDownContent:arrays[2]
        })
            .then((res)=>{
                if(res.data.code === 0){
                    localStorage.setItem('articleContent','');
                }
            })
            .catch((err)=>{
                console.log(err);
            })
    }

    render(){
        return(
            <main id='post'>
                <Milkdown send={(value)=>{this.send(value)}}/>
            </main>
        )
    }
}

export default Admin;


